@media(max-width: 675px) {
    .orientacao {
        margin-top: 15px;
    }



    .area-perguntas {
        padding: 7% 10px;
        align-content: center;
        align-items: center;
        background-color: #1c1c1c;
        border: 3px solid black;
        border-radius: 10px;
        width: 95%;
        margin: 0 auto;
        text-align: center;
    }

    .area-perguntas p {
        font-size: 1.5em;
    }
    .area-perguntas button {
        color: #ADD8E6;
        background-color: gray;
        padding: 5px;
        width: 100px;
        margin: 0 10px;
        border-radius: 10px;
    }

    .area-perguntas button:hover {
        color: white;
        background-color: teal;
        opacity: 0.5;
        padding: 5px;
        width: 100px;
        margin: 0 10px;
        border-radius: 10px;
    }





    .area-resultado {
        padding: 7% 10px;
        background-color: #1c1c1c;
        border: 3px solid #ADD8E6;
        border-radius: 10px;
        width: 95%;
        margin: 0 auto;
        min-height: 100px;
    }

    .resultado {
        margin-top: 15px;    
        display: flex;
        align-content: center;
        align-items: center;
    }

    .resultado h3 {
        text-align: center;
        width: 100%;
    }

    .resultado span {
        color: yellow;
    }

    .texto-resultado p {
        text-align: center;
    }

    .botao {
        margin: 15px 0;
        text-align: center;
    }
    .botao a {
        background-color:#ADD8E6;
        padding: 10px;
        border-radius: 5px;
        color: black;
    }

    .botao a:hover {
        background-color: #1c1c1c;
        padding: 10px;
        border-radius: 5px;
        color: white;
        text-decoration: none;
        border: 2px solid #ADD8E6;
    }

    .info {
        margin: 20px auto;
        padding: 15px;
        background-color: #ADD8E6;
        width: 95%;
        color: black;
        border-radius: 10px;
        border: 3px solid black;
    }
}



