@media(max-width: 675px) { 
    footer {
        text-align: justify;
        padding: 10px;
        margin-top: 20px;
        background-color: #000;
        border-top: 3px solid #add8e6; 
        align-content: center;
    }

    .sobre-site {
        width: 90%;
        font-size: 0.7em;
        margin: 0 auto;
    }

    .sobre-site a {
        color: chartreuse;
    }

    .sobre-autor {
        width: 90%;
        font-size: 0.7em;
        margin: 0 auto;
    }

    .sobre-autor a {
        color: chartreuse;
    }

    .destaque {
        color: chartreuse;
    }
}

@media(min-width: 676px) {
    footer {
        padding: 10px;
        margin-top: 20px;
        background-color: #000;
        border-top: 3px solid #add8e6; 
        display: flex;
        align-items: center;
        align-content: center;
    }
    
    .sobre-site {
        width: 45%;
        font-size: 0.7em;
        margin-right: 10%;
        margin-left: 10%;
    }
    
    .sobre-autor {
        width: 45%;
        font-size: 0.7em;
    }

    .sobre-site a {
        color: chartreuse;
    }

    .sobre-autor {
        width: 90%;
        font-size: 0.7em;
        margin: 0 auto;
    }

    .sobre-autor a {
        color: chartreuse;
    }

    .destaque {
        color: chartreuse;
    }
}